

.navbar {
    box-shadow: none !important;

}
.nav_link {
    color:#F0EDFF !important;
    font-weight: 300 !important;
}
.nav_link:hover {
    color:#fff !important;
    font-weight: 400 !important;
}
.btn_outline_light {
    opacity: 0.9;
}
.main-txt {
    font-size: 40px;
    font-weight: 400;
}
.box_a_1,.box_a_2,.box_a_3 {
    height: 340px;
    width: 360px;
    border: 1px solid #fff;
    border-radius: 25px;
}
.box_img {
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    height : 180px ;
    width : 100% ; 
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
}
.box_img1 {
    background-image: url("https://res.cloudinary.com/practicaldev/image/fetch/s--SqP-M2VI--/c_limit%2Cf_auto%2Cfl_progressive%2Cq_auto%2Cw_880/https://github.com/Binaryify/OneDark-Pro/raw/HEAD/screenshots/editordarker.png");
}
.box_img2 {
    background-image: url("https://miro.medium.com/max/1012/1*vqugCcVCqfb-HSA0p-1Wiw.png");
}
.box_img3 {
    background-image: url("https://www.oxfordwebstudio.com/user/pages/06.da-li-znate/sta-je-css/sta-je-css.png");
}

.box_title {
    font-size: 1.6rem;
    font-weight: 400;
    letter-spacing: 1px;
}
.categorie{
    font-weight: 400;
    font-size: 1.2rem !important;
    opacity: 0.5;
}
.active-categorie {
    opacity: 1;
}


@media only screen and (max-width: 600px) { 
    .header_txt_1 {
        font-size: 40px;
        text-align: center;
    }
    .header_txt_2 {
        font-size: 32px;
        text-align: center;
    }
 }